.md h1, .md h2, .md h3, .md h4, .md h5, .md h6 {
  font-size: 1em;
  margin: 0 0 .3em;
  text-align: left;
}
.md h1 { 
  font-size: 1.9em; 
  text-align: center; 
}
.md h2 { font-size: 1.4em; }
.md h3 { font-size: 1.3em; }
.md h4 { font-size: 1.2em; }
.md h5 { font-size: 1.1em; }

.md hr {
  clear: both;
}

.md { 
  position: relative;
  text-align: justify;
  height: 100%;
}
.md .center { text-align: center; margin:0; }
.md .right { text-align: right; margin:0; }
.md .left { text-align: left; margin:0; }
.md table { text-align: left; }

.md ul, .md ol {margin:0.5em 0 0 0}
.md ul ul, .md ol ul {margin:0}

.md blockquote {
  border-left: 5px solid #999;
  margin: 0 0.5em;
  padding: 0 1em;
}
.md code:not(.hljs) { 
  color: #800;
}
.md pre { 
  margin:0; 
  display: grid;
  overflow: auto;
  font-size: .8em;
  tab-size: 2;
}

.md .fa.left,
.md .fa.fa-left {
  float: left;
  margin-right: .25em;
}
.md .fa.right,
.md .fa.fa-right {
  float: right;
  margin-left: .25em;
}

.md .fa {
  font-size: 1.2em;
  vertical-align: text-bottom;
}
.md .fa-2x {
  font-size: 2em;
}
.md .fa-3x {
  font-size: 3em;
}
.md .fa-4x {
  font-size: 4em;
}
.md .fa-5x {
  font-size: 5em;
}

.md img { max-width:100%; }

.md table { text-align: left; border-spacing:0; font-size:1em; margin: auto; }
.md table.left { text-align: left; margin: unset; }
.md table.fullpage { width:100% }
.md table tr:nth-child(odd) { background:#eee; }
.md table thead tr td { background:#ddf; border-bottom:1px solid #369; }
.md table tr:last-child td { border-bottom:1px solid #369; }
.md table td { padding:0 0.5em; }
.md table td.center { text-align:center }

.md audio { vertical-align: middle }

/* blocks */
.md .floatRight {
  float: right;
  margin: 0 0 0 .5em;
}
.md .floatLeft {
  float: left;
  margin: 0 .5em 0 0;
}
.md iframe.twitter-grid {
  display: inline-block!important;
}
.md iframe.facebook-share-button,
.md iframe.twitter-share-button {
  vertical-align: middle;
  border:none;
  overflow:hidden;
  display: inline-block!important;
  visibility: visible!important;
  width: 76px!important;
  height: 28px!important;
}
.md twitter-widget {
  display: inline-block!important;
  min-width: 200px;
}
/* Editor */
.mdEditDialog {
  content:"";
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  color: #666;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.5);
  z-index: 9999;
}
textarea.mdEditor {
  -moz-tab-size : 4;
  -o-tab-size : 4;
  tab-size : 4;
}
.mdEditDialog h1 {
  margin: 0 0 .5em;
  font-size: 1.3em;
}
.mdEditDialog > div {
  position: fixed;
  top: 30%;
  left: 50%;
  -webkit-transform: translate(-50%, -30%);
  transform: translate(-50%, -30%);
  background-color: #fff;
  box-shadow: 5px 5px 5px rgba(0,0,0,.5);
  padding: 1em;
  z-index: 9999;
}
.mdEditDialog label {
  display: block;
  padding-top: .5em;
}
.mdEditDialog input[type='text'] {
  display: block;
  width: 400px;
}
.mdEditDialog input[type='number'] {
  width: 5em;
}
.mdEditDialog .mderror {
  -webkit-box-shadow: 0 0 1px 1px red;
  box-shadow: 0 0 1px 1px red;
}
.mdEditDialog input[type='submit'],
.mdEditDialog input[type='button'] {
  float: right;
  background: #369;
  color: #fff;
  border: 0;
  font-size: 1em;
  padding: 0 1em;
  margin: 1em 0 0 .5em;
}
.mdEditDialog.twitter i.twitter {
  display: block;
  font-size: .8em;
  border-left: 5px solid #ccc;
  padding-left: .5em;
  margin-left: .5em;
}
.mdEditDialog span {
  padding-top: .5em;
}
.mdEditDialog .mdimgsize {
  display: none;
}
.mdEditDialog.media .mdimgsize {
  display: inline-block;
}
.mdEditDialog .twitter,
.mdEditDialog.twitter .mdalt {
  display: none;
}
.mdEditDialog.twitter .twitter,
.mdEditDialog.twitter input.mdimgsize {
  display: inline-block;
}


/* Edit bar */

.mdEditBar {
  background: #eee;
  float: left;
  color: #333;
}
.mdEditBar i {
  padding: 5px 7px;
  font-size: .9em;
  cursor: pointer;
  display: inline-block;
  height: 1em;
  width: 1em;
  text-align: center;
  text-shadow: 2px 2px 1px #fff;
  vertical-align: middle;
}
.mdEditBar i.title:before {
  content: "T";
  font-weight: bold;
  font-family: serif;
  font-size: 1.4em;
  display: inline-block;
  margin: -.15em;
}
.mdEditBar i.line:before {
  content: "";
  height: 1px;
  background: #111;
  padding: 0;
  box-shadow: 1px 1px 2px 1px #fff;
  width: 100%;
  display: block;
  margin: .4em 0;
}
.mdEditBar i.separator {
  width: 1px;
  background: #ccc;
  padding: 0;
  height: 1.2em;
  box-shadow: 1px 0px 2px 1px #fff;
  margin: 0 2px;
}

.mdEditDialog.iconDlg i {
  padding: .3em;
  width: 7%;
  height: 1em;
  text-align: center;
}
.mdEditDialog.iconDlg i:hover {
  box-shadow: 1px 1px 5px rgba(0,0,0,.5);
  z-index: 1;
  cursor: pointer;
  transform: scale(1.2);
}

iframe.github-btn {
  width: 160px;
  height: 30px;
  vertical-align: middle;
  display: inline;
}

.md kbd {
  background-color: #eee;
  padding: 0 .3em;
  display: inline-block;
  box-shadow: inset 1px 1px 3px 2px #fff, 5px 5px 5px #999, 0px 0px 0 2px #999;
  border-radius: 5px /100%;
  margin: 0 .3em 0 .2em;
  text-shadow: 2px 2px #fff;
  font-size: .8em;
  vertical-align: middle;
  margin-top: -4px;
  box-sizing: border-box;
  min-width: 1.3em;
  text-align: center;
}